<template>
  <div class="login-page">
    <van-nav-bar title="面经登录" />
    <van-form @submit="onSubmit">
      <van-field
        label="用户名"
        placeholder="用户名"
        name="username"
        v-model="username"
        :rules="[
          { required: true, message: '请输入用户名' },
          { pattern: /^\w{6,16}$/, message: '用户名必须为6-16位' },
        ]"
      ></van-field>
      <van-field
        label="密码"
        type="password"
        placeholder="密码"
        name="password"
        v-model="password"
        :rules="[
          { required: true, message: '请输入密码' },
          { pattern: /^\w{6,16}$/, message: '用户名必须为6-16位' },
        ]"
      ></van-field>
      <div style="margin: 16px">
        <van-button type="info" block>登录</van-button>
      </div>
    </van-form>
    <router-link to="/register" class="link">注册账号</router-link>
  </div>
</template>

<script>
import { login } from '@/api/user'
import { setToken } from '@/utils/storage'

export default {
  name: 'login-page',
  data() {
    return {
      username: '',
      password: '',
    }
  },
  methods: {
    async onSubmit(value) {
      const {
        data: { token },
      } = await login(value)
      this.$toast.success('登录成功')
      setToken(token)
      this.$router.push('/')
    },
  },
}
</script>

<style lang="less" scoped>
.link {
  color: #069;
  font-size: 12px;
  padding-right: 20px;
  float: right;
}
</style>
